import { Component } from '@angular/core';
import { Router } from '@angular/router';
import { LoginEOSService } from '@cryptolions/ulm-eosio';
import { ContractApiService } from 'src/app/core/services/contact.services/contract.api.service';

@Component({
  selector: 'eos-root',
  templateUrl: './eos.component.html',
  styleUrls: ['./eos.component.scss'],
})
export class EosPageComponent {
  public account_name = '';
  constructor(public contract: ContractApiService, public loginEOSService: LoginEOSService, private router: Router){
    this.contract.changeApi(this.router.url.split('/')[1].toUpperCase())
    loginEOSService.loggedIn.subscribe(res => {
      console.log(loginEOSService);
      this.account_name = loginEOSService.accountName;
      this.contract.changeUserName(loginEOSService.accountName);
      // this.contract.changeApi(loginEOSService.eosioWalletType.toUpperCase());
    });
  }
  login(){
    console.log('g');
  }
}
